const StoryContent = () => {
  const content = (
    <div className="bx--grid">
      <div className="bx--row">
        <section className="bx--offset-lg-3 bx--col-lg-13">
          <h2
            style={{
              fontWeight: "800",
              margin: "30px 0",
              fontSize: "20px"
            }}
          >
            Немного о системах управления контентом
          </h2>
          <p style={{ lineHeight: "20px" }}>
            СMS (система управления контентом) - это программное обеспечение,
            которое позволяет упростить создание, редактирование и управление
            веб-сайтом или другими типами контента. Она предоставляет
            пользовательский интерфейс, который позволяет авторам и редакторам
            создавать и публиковать контент без необходимости знания
            программирования или веб-разработки.
            <br />
            <br />
            Одной из основных преимуществ CMS является возможность быстрого и
            удобного обновления контента. С помощью CMS вы можете добавлять
            новые страницы, редактировать текст, загружать изображения и видео,
            создавать блоги и многое другое. Это позволяет поддерживать ваш
            веб-сайт актуальным и интересным для посетителей.
            <br />
            <br />
            CMS также обеспечивает удобное управление структурой сайта. Вы
            можете создавать меню, добавлять и редактировать разделы и
            подразделы, устанавливать права доступа для пользователей и многое
            другое. Это помогает организовать контент и обеспечивает навигацию
            по сайту для пользователей.
          </p>
          <h2
            style={{
              fontWeight: "800",
              margin: "30px 0",
              fontSize: "20px"
            }}
          >
            Удобство и простота
          </h2>
          <p style={{ lineHeight: "20px" }}>
            Удобство CMS заключается в том, что она предоставляет простой и
            интуитивно понятный интерфейс для создания, редактирования и
            управления контентом на веб-сайте.
            <br />
            <br />
            С помощью CMS вы можете легко добавлять новые страницы,
            редактировать текст, загружать изображения и видео, создавать блоги
            и многое другое, не имея специальных навыков веб-разработки. Это
            особенно полезно для непрофессионалов, которые хотят самостоятельно
            управлять своим веб-сайтом без необходимости обращаться к
            разработчикам.
            <br />
            <br />
            CMS также обеспечивает удобное управление структурой сайта. Вы
            можете создавать и редактировать меню, добавлять и изменять разделы
            и подразделы, легко организовывать контент на вашем сайте. Это
            позволяет создавать навигацию, которая легко понятна для
            пользователей, и помогает им быстро находить нужную информацию.
          </p>
          <h2
            style={{
              fontWeight: "800",
              margin: "30px 0",
              fontSize: "20px"
            }}
          >
            Дополнительные возможности
          </h2>
          <p style={{ lineHeight: "20px" }}>
            Кроме того, CMS обеспечивает безопасность вашего веб-сайта. Она
            предлагает механизмы защиты от взлома и злоумышленников, такие как
            системы аутентификации и контроля доступа. Это позволяет вам быть
            уверенными в безопасности вашего сайта и защитить его от
            потенциальных угроз.
          </p>
        </section>
      </div>
    </div>
  );

  return <Content id="main-content">{content}</Content>;
};

